import { Suspense } from 'react';
import { db, products } from '@/lib/db';
import { eq, and, gte, lte, desc, asc, sql } from 'drizzle-orm';
import ProductCard from '@/components/ProductCard';
import { SlidersHorizontal, X } from 'lucide-react';
import { type InferModel } from 'drizzle-orm';

type Product = InferModel<typeof products>;

interface ProductsPageProps {
  searchParams: {
    category?: string;
    minPrice?: string;
    maxPrice?: string;
    sortBy?: string;
    sortOrder?: string;
  };
}

async function getProducts(searchParams: ProductsPageProps['searchParams']) {
  const conditions = [];

  if (searchParams.category) {
    conditions.push(eq(products.category, searchParams.category));
  }

  if (searchParams.minPrice) {
    conditions.push(gte(products.price, parseFloat(searchParams.minPrice)));
  }

  if (searchParams.maxPrice) {
    conditions.push(lte(products.price, parseFloat(searchParams.maxPrice)));
  }

  const whereClause = conditions.length > 0 ? and(...conditions) : undefined;

  let orderByClause;
  if (searchParams.sortBy) {
    const order = searchParams.sortOrder === 'desc' ? desc : asc;
    switch (searchParams.sortBy) {
      case 'price':
        orderByClause = order(products.price);
        break;
      case 'rating':
        orderByClause = order(products.rating);
        break;
      case 'name':
        orderByClause = order(products.name);
        break;
      default:
        orderByClause = desc(products.createdAt);
    }
  } else {
    orderByClause = desc(products.createdAt);
  }

  return db.select().from(products).where(whereClause).orderBy(orderByClause);
}

function ProductsSkeleton() {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
      {[1, 2, 3, 4, 5, 6].map((i) => (
        <div key={i} className="bg-white rounded-lg shadow-md overflow-hidden animate-pulse">
          <div className="aspect-[4/3] bg-gray-200" />
          <div className="p-4">
            <div className="h-6 bg-gray-200 rounded w-3/4 mb-2" />
            <div className="h-4 bg-gray-200 rounded w-full mb-4" />
            <div className="flex justify-between items-center">
              <div className="h-6 bg-gray-200 rounded w-1/4" />
              <div className="h-6 bg-gray-200 rounded w-1/4" />
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}

export default async function ProductsPage({ searchParams }: ProductsPageProps) {
  const productsList = await getProducts(searchParams);
  
  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">All Products</h1>
          <p className="mt-2 text-gray-600">Browse our collection of Playmais construction sets</p>
        </div>

        <div className="flex flex-col md:flex-row gap-8">
          {/* Products grid */}
          <div className="flex-1">
            <Suspense fallback={<ProductsSkeleton />}>
              {productsList.length > 0 ? (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                  {productsList.map(product => (
                    <ProductCard 
                      key={product.id} 
                      product={product}
                    />
                  ))}
                </div>
              ) : (
                <div className="text-center py-12">
                  <p className="text-gray-500">No products found matching your criteria.</p>
                </div>
              )}
            </Suspense>
          </div>
        </div>
      </div>
    </div>
  );
} 